.module__start__section {
    max-width: 700px;
    max-height: 300px;
    width: 60vw;
    height: auto;
    margin: 0 auto;
    padding: 2rem;
    box-shadow: 3px 4px 10px 1px rgba(0, 0, 0, 0.3);
    position: relative;
    z-index: 2;
}

.module__start__section button {
    outline: none;
    background: none;
    font-weight: 700;
    font-size: 1.1rem;
    box-shadow: none;
    border: none;
}

.tab1 {
    max-width: 500px;
    max-height: 300px;
    display: block;
    position: relative;
    width: 40vw;
    height: 20px;
    background: #565788;
    margin: 0 auto;
    border-radius: 10px 10px 0 0;
}

.tab2 {
    max-width: 600px;
    max-height: 300px;
    display: block;
    position: relative;
    width: 50vw;
    height: 20px;
    background: #8588A1;
    margin: 0 auto;
    border-radius: 10px 10px 0 0;
}

.tab3 {
    max-width: 700px;
    max-height: 300px;
    display: block;
    position: relative;
    width: 60vw;
    height: 20px;
    background: #BCBDC7;
    margin: 0 auto;
    border-radius: 10px 10px 0 0;
}

.progress__bar {
    max-width: 700px;
    max-height: 300px;
    display: block;
    position: relative;
    width: 60vw;
    height: 10px;
    background: #ccc;
    margin: 0 auto;
    border-radius: 0 0 10px 10px;
    margin-bottom: 3rem;
}

.progress__bar__level {
    max-width: 700px;
    max-height: 300px;
    display: block;
    position: relative;
    width: 60vw;
    height: 10px;
    background: #71A068;
    margin: 0 auto 0 0;
    border-radius: 0 0 10px 10px;
    margin-bottom: 3rem;
    transition: width ease-in-out 300ms;
}